

































import Vue from 'vue'
import { mapGetters } from 'vuex'
import Chart from '@/shared/components/Chart.vue'

export default Vue.extend({
  name: 'CovidChart',

  components: {
    Chart
  },

  data: () => ({
    defaultNotification: 'Data unavailable'
  }),

  computed: {
    ...mapGetters([
      'getSelectedGraphType',
      'getCovidChartLabels',
      'getCovidChartData',
      'getSelectedCountry'
    ])
  }
})
